.app-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
  padding-bottom: 0.25rem;
  position: relative;
}

.app-header-underline {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 2px;
  background: #000000;
  animation: trace-line 1s ease-in-out 1s forwards;
}

@keyframes trace-line {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}

.app-header img {
  display: block;
  width: 64px;
  height: auto;
}

.app-header h1 {
  font-size: 1.5rem;
  font-weight: 700;
  letter-spacing: 2px;
}
