@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&family=Noto+Sans:ital,wght@1,300&display=swap');

.random-civ {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  margin: 1rem 0;
}

.random-civ-name {
  font-family: 'Great Vibes', sans-serif;
  font-size: 2.5rem;
  letter-spacing: 1px;
  margin: 1rem 0;
}

.random-civ-flag {
  display: block;
  width: 128px;
  height: 96px;
  cursor: pointer;
}

.hint {
  font-family: 'Noto Sans', sans-serif;
  font-size: 0.8rem;
  font-style: italic;
  color: #2a202d;
}
